import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiFlyingFlag } from 'react-icons/gi';
import { calculate } from '../databases/Calculate';
import { keywordDatabase } from '../databases/Keywords';
const iconStyle = { marginBottom: '0.2em' };

const UnitCard = ({ name, keywords, index }) => {
  const unit = calculate(name, keywords);
  return (
    <>
      <div className='card border-white mb-2'>
        <div className='card-header bg-transparent d-flex px-0'>
          <button className='btn text-left flex-grow-1 collapsed' data-toggle='collapse' data-target={'#num' + index + '-collapse'}>
            {unit.name} <small className='text-muted'>/ {unit.points} pts</small>
          </button>
          <small className='text-muted pt-2 pr-3'>{unit.type}</small>
        </div>
        <div className='list-group list-group-flush collapse' id={'num' + index + '-collapse'}>
          <div className='list-group-item list-group-item-light d-flex'>
            <small className='flex-grow-1'>{unit.type === 'General' || unit.type === 'Hero' || unit.type === 'Wizard' ? unit.type + ' character.' : unit.size === 1 ? 'One stand ' + unit.type + ' unit.' : unit.size === 2 ? 'Two stand ' + unit.type + ' unit.' : unit.size === 3 ? 'Three stand ' + unit.type + ' unit.' : ''}</small>
            <small>Warmaster Pocket</small>
          </div>
          <li className='list-group-item'>
            {unit.attacks ? (
              <small className='pr-1'>
                (<GiMailedFist style={iconStyle} /> {unit.attacks})
              </small>
            ) : (
              ''
            )}
            {unit.shots ? (
              <small className='px-1'>
                (<GiPocketBow style={iconStyle} /> {unit.shots}
                {unit.shotsRandom ? 'd6' : ''} /
                <small className='ml-1 text-muted'>
                  {unit.shotsRange}cm{unit.shotsOmni ? ' 360°' : ''}
                </small>
                )
              </small>
            ) : (
              ''
            )}
            {unit.command ? (
              <small className='px-1'>
                (<GiFlyingFlag style={iconStyle} /> {unit.command} /<small className='ml-1 text-muted'>{unit.commandRange}cm</small>)
              </small>
            ) : (
              ''
            )}
            {unit.hits ? (
              <small className='px-1'>
                (<GiHeartPlus style={iconStyle} /> {unit.hits})
              </small>
            ) : (
              ''
            )}
            {unit.armour === 7 ? (
              ''
            ) : unit.armour ? (
              <small className='px-1'>
                (<GiCheckedShield style={iconStyle} /> {unit.armour}+)
              </small>
            ) : (
              ''
            )}
          </li>
          {unit.rules.map((e) =>
            keywordDatabase.rules.map((f) =>
              e === f.name ? (
                <li className='list-group-item'>
                  <small className='card-text'>
                    <strong>{f.name}.</strong> {f.text}
                  </small>
                </li>
              ) : (
                ''
              )
            )
          )}
        </div>
      </div>
    </>
  );
};
export default UnitCard;
