import React, { useState, useEffect } from 'react';
import { armyDatabase } from './databases/Armies';
import { BsArrowRight } from 'react-icons/bs';
import Sidebar from './components/Sidebar';
import Page from './components/Page';
import UnitCard from './components/UnitCard';
import Calculator from './components/Calculator';

const iconStyle = { fontSize: '0.75em', marginBottom: '0.1em' };

function App() {
  // The App's state, which let's the app know which page to mount.
  const [currentPage, setCurrentPage] = useState('Home');
  // Whenever the state updates (i.e. user clicks a page link), refresh the app to apply the new Page component.
  useEffect(() => {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
  }, [currentPage]);
  return (
    <div>
      {/* Mount the Sidebar Menu component. */}
      <Sidebar setPage={setCurrentPage} />
      {/* If the currentPage state matches one of the Page components below, mount that Page component. */}
      {/* Home page */}
      {currentPage === 'Home' ? (
        <Page title='Home'>
          <h3 className='display-4 mb-3'>Warmaster Pocket</h3>
          <p>A new way to enjoy the 10mm fantasy miniatures hobby.</p>
          <p>Warmaster Pocket is a collection of homebrewed content for the Warmaster tabletop miniatures game.</p>
          <small>
            <em>
              Post your Warmaster Pocket feedback and feature requests in the #homebrew-ideas-and-lists channel on the{' '}
              <a href='https://discord.gg/pB2uR7kcvv' target='_blank'>
                Warmaster Revolution discord!
              </a>
            </em>
          </small>
        </Page>
      ) : (
        ''
      )}
      {/* Intro page */}
      {currentPage === 'Intro' ? (
        <Page title='Intro'>
          <h2 className='display-4 mb-3'>Introduction</h2>
          <p>Warmaster Pocket is a 10mm-scale miniature wargame. It's played on a tabletop with your buddies for fun.</p>
          <h3 className='lead'>Dice</h3>
          <p>This game uses six-sided dice.</p>
          <h3 className='lead'>Distances</h3>
          <p>This game measures all distances in metric.</p>
          <h3 className='lead'>Rules</h3>
          <p>
            This game's armies are homebrewed to be compatible with the{' '}
            <a href='https://www.wm-revolution.com/' target='_blank'>
              Warmaster Revolution Compendium rulebook.
            </a>
          </p>
          <aside className='px-4 pt-3 mb-3 border'>
            <h3 className='h6'>Where to Get Miniatures?</h3>
            <p>Use tokens or papercraft miniatures to get started playing as quickly as possible, or you can buy 10mm fantasy miniatures from your local hobby store, from small online shops, or even 3D print your own.</p>
            <small>
              <p className='text-muted text-center'>
                If you want to include your minis storefront here, contact me via the{' '}
                <a className='link-secondary' href='https://discord.gg/pB2uR7kcvv' target='_blank'>
                  Warmaster Revolution discord!
                </a>
              </p>
            </small>
          </aside>
          <hr />
          <div className='text-center'>
            <button className='btn' onClick={() => setCurrentPage('Armies')}>
              Check out the armies <BsArrowRight />
            </button>
          </div>
        </Page>
      ) : (
        ''
      )}
      {/* Armies page */}
      {currentPage === 'Armies' ? (
        <Page title='Armies'>
          <h3 className='display-4 mb-3'>Total War: Warmaster</h3>
          <p>Play as your favourite armies from the video game.</p>
          <div className='list-group list-group-flush'>
            {armyDatabase.map((e) => (
              <>
                <button className='btn list-group-item text-left mb-1 d-flex' onClick={() => setCurrentPage(e.name)}>
                  <span className='flex-grow-1'>{e.name}</span>
                  <small className='text-muted'></small>
                </button>
              </>
            ))}
          </div>
        </Page>
      ) : (
        ''
      )}
      {/* Each army list */}
      {armyDatabase.map((e) =>
        currentPage === e.name ? (
          <Page title={e.name}>
            <h3 className='display-4 mb-3'>{e.name}</h3>
            <p>{e.text}</p>
            {e.units.map((f, index) => (
              <UnitCard name={f.name} keywords={f.keywords} index={index} />
            ))}
            <div className='text-center'>
              <button className='btn' onClick={() => setCurrentPage('Armies')}>
                Return <BsArrowRight />
              </button>
            </div>
          </Page>
        ) : (
          ''
        )
      )}
      {/* Calculator */}
      {currentPage === 'Calculator' ? (
        <Page title='Calculator'>
          <h3 className='display-4 mb-3'>Custom Unit Calculator</h3>
          <p>Create your own custom units for casual play.</p>
          <Calculator />
        </Page>
      ) : (
        ''
      )}
      {/* Credits page */}
      {currentPage === 'Credits' ? (
        <Page title='Credits'>
          <h3 className='display-4 mb-3'>Thank You</h3>
          <p>Warmaster Pocket is a homebrew of the Warmaster tabletop rules.</p>
          <div className='text-muted'>
            <p>All rules writing and app development was done by Calvin St.</p>
            <p>Special thanks to the awesome people of the Warmaster Revolution discord for their combined encouragement and feedback throughout this app's development.</p>
            <p>Thanks to the friendly people of the WMRX forum for workshoping ideas with me.</p>
            <p>Icons from game-icons.net under CC BY 3.0 – thanks to the talented artists: sbed, Lorc, Zeromancer, and Delapouite.</p>
            <small>
              <em>Warmaster Pocket's game design was inspired by the fan-edition of Warmaster Revolution v1.1, the original game design for Warmaster was written by Rick Priestley and published by Games Workshop. This website is completely unofficial and in no way endorsed by Games Workshop Limited.</em>
            </small>
          </div>
        </Page>
      ) : (
        ''
      )}
    </div>
  );
}

export default App;
