import { keywordDatabase } from './Keywords';

export const calculate = (inputName, inputKeywords) => {
  if (!inputName) {
    inputName = '';
  }
  if (!inputKeywords) {
    inputKeywords = [];
  }
  // Create an empty unit.
  let outputUnit = {
    name: inputName,
    type: '',
    attacks: 0,
    shots: 0,
    shotsRange: 0,
    shotsRandom: false,
    shotsOmni: false,
    hits: 0,
    armour: 7,
    command: 0,
    commandRange: 0,
    size: 3,
    points: 0,
    rules: [],
  };
  // Create an empty array to store rules into later.
  let outputKeywords = [];
  // Prepare the data funnels.
  const updateOuputUnit = (databaseElement) => {
    if (databaseElement.type) {
      outputUnit.type = databaseElement.type;
    }
    if (databaseElement.attacks) {
      outputUnit.attacks += databaseElement.attacks;
    }
    if (databaseElement.shots) {
      outputUnit.shots += databaseElement.shots;
    }
    if (databaseElement.shotsRange) {
      outputUnit.shotsRange += databaseElement.shotsRange;
    }
    if (databaseElement.shotsRandom) {
      outputUnit.shotsRandom = databaseElement.shotsRandom;
    }
    if (databaseElement.shotsOmni) {
      outputUnit.shotsOmni = databaseElement.shotsOmni;
    }
    if (databaseElement.hits) {
      outputUnit.hits += databaseElement.hits;
    }
    if (databaseElement.armour) {
      outputUnit.armour = databaseElement.armour;
    }
    if (databaseElement.command) {
      outputUnit.command = databaseElement.command;
    }
    if (databaseElement.commandRange) {
      outputUnit.commandRange += databaseElement.commandRange;
    }
    if (databaseElement.size) {
      outputUnit.size = databaseElement.size;
    }
    if (databaseElement.points) {
      outputUnit.points += databaseElement.points;
    }
    if (databaseElement.rules) {
      outputKeywords.push(...databaseElement.rules);
    }
  };
  // Search each database for any entries that match the keywords, apply all math using updateOutputUnit(), temporarily store all special rules into outputKeywords[].
  inputKeywords.map((inputElement) => (
    <>
      {keywordDatabase.type.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.attacks.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.shots.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.hits.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.armour.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.command.map((databaseElement) => (databaseElement.name === inputElement ? updateOuputUnit(databaseElement) : ''))}
      {keywordDatabase.rules.map((databaseElement) => (databaseElement.name === inputElement ? outputKeywords.push(databaseElement.name) : ''))}
    </>
  ));
  // Trim any duplicate abilities before applying to the final object.
  const updateOutputKeywords = (keywords) => {
    const cleanList = [...new Set(keywords)];
    cleanList.map((cleanListElement) => keywordDatabase.rules.map((databaseElement) => (databaseElement.name === cleanListElement ? (outputUnit.points += databaseElement.points) : '')));
    outputUnit.rules = cleanList;
  };
  updateOutputKeywords(outputKeywords);
  // Apply final points adjustments.
  // Too powerful? More than 1 strike adds a points tax.
  let strikes = 0;
  if (outputUnit.type === 'Monster Detachment' || outputUnit.type === 'Chariot Detachment') {
    strikes += outputUnit.attacks > 2 ? 1 : 0;
    strikes += outputUnit.hits > 6 ? 1 : 0;
    strikes += outputUnit.armour < 6 ? 1 : 0;
  } else {
    strikes += outputUnit.attacks > 3 ? 1 : 0;
    strikes += outputUnit.hits > 3 ? 1 : 0;
    strikes += outputUnit.armour < 6 ? 1 : 0;
  }
  if (strikes === 2) {
    outputUnit.points += 25;
  }
  if (strikes > 2) {
    outputUnit.points += 45;
  }
  // Way too powerful? More than 1 flag adds another points tax.
  let flags = 0;
  if (outputUnit.type !== 'Monster Detachment' && outputUnit.type !== 'Chariot Detachment') {
    flags += outputUnit.attacks > 4 ? 1 : 0;
    flags += outputUnit.hits > 3 ? 1 : 0;
    flags += outputUnit.armour < 5 ? 1 : 0;
  }
  if (flags > 1) {
    outputUnit.points += 20;
  }
  // Not powerful enough? Add reduce points to all that apply.
  if (outputUnit.type > 30 && (outputUnit.type === 'Infantry Detachment' || outputUnit.type === 'Cavalry Detachment')) {
    outputUnit.points -= 15;
  }
  if (outputUnit.type === 'Cavalry' && outputUnit.armour > 4) {
    outputUnit.points -= 15;
  }
  if (strikes > 1 && outputUnit.type === 'Infantry') {
    outputUnit.points -= 25;
  }
  // Tell the beautiful person to have a great day
  return outputUnit;
};
