import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiFlyingFlag } from 'react-icons/gi';
import { useState } from 'react';
import { calculate } from '../databases/Calculate';
import { keywordDatabase } from '../databases/Keywords';

const iconStyle = { marginBottom: '0.2em' };

const Calculator = () => {
  const [unit, setUnit] = useState({});
  console.log(unit);

  const handleChange = () => {
    const name = document.getElementById('unitName').value;
    const type = document.getElementById('unitType').value;
    const attacks = document.getElementById('unitAttacks').value;
    const shots = document.getElementById('unitShots').value;
    const hits = document.getElementById('unitHits').value;
    const armour = document.getElementById('unitArmour').value;
    const command = document.getElementById('unitCommand').value;
    const rules1 = document.getElementById('unitRules1').value;
    const rules2 = document.getElementById('unitRules2').value;
    const keywords = [type, attacks, shots, hits, armour, command, rules1, rules2];
    const newUnit = calculate(name, keywords);
    setUnit(newUnit);
  };
  const handleReset = () => {
    document.getElementById('unitAttacks').selectedIndex = 0;
    document.getElementById('unitShots').selectedIndex = 0;
    document.getElementById('unitHits').selectedIndex = 0;
    document.getElementById('unitArmour').selectedIndex = 0;
    document.getElementById('unitCommand').selectedIndex = 0;
    document.getElementById('unitRules1').selectedIndex = 0;
    document.getElementById('unitRules2').selectedIndex = 0;
    handleChange();
  };
  return (
    <>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Name</span>
        <input type='text' className='form-control' placeholder='Custom Name' id='unitName' onChange={(e) => handleChange()} />
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Type</span>
        <select className='form-select' id='unitType' onChange={(e) => handleReset()}>
          <option value='–'>–</option>
          {keywordDatabase.type.map((e) => (
            <option value={e.name}>{e.name}</option>
          ))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Attacks</span>
        <select className='form-select' id='unitAttacks' onChange={(e) => handleChange()}>
          <option value='–'>–</option>
          {keywordDatabase.attacks.map((e) => e.types.map((f) => (f === unit.type ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Shots</span>
        <select className='form-select' id='unitShots' onChange={(e) => handleChange()}>
          <option value='–'>–</option>
          {keywordDatabase.shots.map((e) => e.types.map((f) => (f === unit.type ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? (unit.type !== 'General' && unit.type !== 'Hero' && unit.type !== 'Wizard' ? 'input-group mb-3' : 'input-group mb-3 d-none') : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Hits</span>
        <select className='form-select' id='unitHits' onChange={(e) => handleChange()}>
          <option value='–'>–</option>
          {keywordDatabase.hits.map((e) => e.types.map((f) => (f === unit.type ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? (unit.type !== 'General' && unit.type !== 'Hero' && unit.type !== 'Wizard' ? 'input-group mb-3' : 'input-group mb-3 d-none') : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Armour</span>
        <select className='form-select' id='unitArmour' onChange={(e) => handleChange()}>
          <option value='–'>–</option>
          {keywordDatabase.armour.map((e) => e.types.map((f) => (f === unit.type ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type === 'General' || unit.type === 'Hero' || unit.type === 'Wizard' ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Command</span>
        <select className='form-select' id='unitCommand' onChange={(e) => handleChange()}>
          <option value='–'>–</option>
          {keywordDatabase.command.map((e) => e.types.map((f) => (f === unit.type ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Rules #1</span>
        <select className='form-select' id='unitRules1' onChange={(e) => handleChange()}>
          <option value='–'>–</option>
          {keywordDatabase.rules.map((e) => e.types.map((f) => (f === unit.type ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <div className={unit.type ? 'input-group mb-3' : 'input-group mb-3 d-none'}>
        <span className='input-group-text col-3 bg-light'>Rules #2</span>
        <select className='form-select' id='unitRules2' onChange={(e) => handleChange()}>
          <option value='–'>–</option>
          {keywordDatabase.rules.map((e) => e.types.map((f) => (f === unit.type ? <option value={e.name}>{e.name}</option> : '')))}
        </select>
      </div>
      <h3 className='text-center lead mt-5'>Your Custom Unit</h3>
      <div className='card border-white mb-2'>
        <div className='card-header bg-transparent d-flex px-0'>
          <div className='px-3 text-left flex-grow-1'>
            {unit.name} <small className='text-muted'>/ {unit.points} pts</small>
          </div>
        </div>
        <div className='list-group list-group-flush'>
          <div className='list-group-item list-group-item-light d-flex'>
            <small className='flex-grow-1'>{unit.type === 'General' || unit.type === 'Hero' || unit.type === 'Wizard' ? unit.type + ' character.' : unit.size === 1 ? 'One stand ' + unit.type + ' unit.' : unit.size === 2 ? 'Two stand ' + unit.type + ' unit.' : unit.size === 3 ? 'Three stand ' + unit.type + ' unit.' : ''}</small>
            <small>Warmaster Pocket</small>
          </div>
          <li className='list-group-item'>
            {unit.attacks ? (
              <small className='pr-1'>
                (<GiMailedFist style={iconStyle} /> {unit.attacks})
              </small>
            ) : (
              ''
            )}
            {unit.shots ? (
              <small className='px-1'>
                (<GiPocketBow style={iconStyle} /> {unit.shots}
                {unit.shotsRandom ? 'd6' : ''} /
                <small className='ml-1 text-muted'>
                  {unit.shotsRange}cm{unit.shotsOmni ? ' 360°' : ''}
                </small>
                )
              </small>
            ) : (
              ''
            )}
            {unit.command ? (
              <small className='px-1'>
                (<GiFlyingFlag style={iconStyle} /> {unit.command} /<small className='ml-1 text-muted'>{unit.commandRange}cm</small>)
              </small>
            ) : (
              ''
            )}
            {unit.hits ? (
              <small className='px-1'>
                (<GiHeartPlus style={iconStyle} /> {unit.hits})
              </small>
            ) : (
              ''
            )}
            {unit.armour === 7 ? (
              ''
            ) : unit.armour ? (
              <small className='px-1'>
                (<GiCheckedShield style={iconStyle} /> {unit.armour}+)
              </small>
            ) : (
              ''
            )}
          </li>
          {unit.rules
            ? unit.rules.map((e) =>
                keywordDatabase.rules.map((f) =>
                  e === f.name ? (
                    <li className='list-group-item'>
                      <small className='card-text'>
                        <strong>{f.name}.</strong> {f.text}
                      </small>
                    </li>
                  ) : (
                    ''
                  )
                )
              )
            : ''}
        </div>
      </div>
    </>
  );
};

export default Calculator;
